import React from 'react'
import { useNavigate, useParams } from 'react-router-dom';

const WaterTopButtons = (props) => {

    const { id } = useParams()
    const navigate = useNavigate()
    const active = props.active;

    return (
        <>
            <div className="md:flex py-2 justify-center items-center container mx-2 md:mx-auto w-full">
                <div className="flex-1 flex pl-2 font-bold text-xs md:text-2xl text-gray-700"> Consumer Details - {props?.consumerNo}</div>
                <div className="flex justify-right items-center space-x-2">
                    <button onClick={() => navigate(`/waterViewConsumer/${id}`)} className={`font-semibold text-xs md:text-base ${active == "consumer" ? "bg-indigo-500 text-white" : "bg-white text-indigo-500"} border border-indigo-500  px-4 py-1 shadow-lg hover:scale-105 rounded-sm`} >Consumer Details</button>

                    <button onClick={() => navigate(`/waterViewDemand/${id}`)} className={`font-semibold md:text-base text-xs ${active == "demand" ? "bg-indigo-500 text-white" : "bg-white text-indigo-500"} border border-indigo-500  px-4 py-1 shadow-lg hover:scale-105 rounded-sm`} >Demand Details</button>

                    <button onClick={() => navigate(`/waterViewPaymentHistory/${id}`)} className={`font-semibold md:text-base text-xs ${active == "payment" ? "bg-indigo-500 text-white" : "bg-white text-indigo-500"} border border-indigo-500  px-2 py-1 shadow-lg hover:scale-105 rounded-sm`} >Payment History</button>
                </div>
            </div>

            {/* <div>
                <div className="flex py-2 justify-center items-center">
                    <div className="flex-1 flex pl-2 font-bold text-2xl text-gray-700"> Payment History - 85hrjh</div>
                    <div className="flex justify-right items-center">
                        <button onClick={() => navigate(`/waterViewConsumer/${id}`)} className={`mr-4  ${active == "consumer" ? "bg-indigo-500 text-white" : "bg-white text-indigo-500"} px-4 py-1 shadow-lg hover:scale-105 rounded-sm`} >Consumer Details</button>
                        <button onClick={() => navigate(`/waterViewDemand/${id}`)} className={`mr-4  ${active == "demand" ? "bg-indigo-500 text-white" : "bg-white text-indigo-500"} px-4 py-1 shadow-lg hover:scale-105 rounded-sm`} >Demand Details</button>
                        <button onClick={() => navigate(`/waterViewPaymentHistory/${id}`)} className={`mr-4  ${active == "payment" ? "bg-indigo-500 text-white" : "bg-white text-indigo-500"} px-4 py-1 shadow-lg hover:scale-105 rounded-sm`} >Payment History</button>
                    </div>
                </div>
            </div> */}
        </>
    )
}

export default WaterTopButtons
